angular.module('tenthousandthings').run(['$templateCache', function($templateCache) {
  'use strict';

  $templateCache.put('list.html',
    "<div class=\"ttt-list\">\n" +
    "\t<ttt-thing ng-repeat=\"thing in things\" thing=\"thing\"></ttt-thing>\n" +
    "</div>"
  );


  $templateCache.put('thing.html',
    "<article class=\"ttt-thing\">\n" +
    "\t<h3>{{thing.title}}</h3>\n" +
    "</article>"
  );

}]);
